import LogEvent from './LogEvent';
import CircularBuffer from './CircularBuffer';

/**
 * LogManager allows multiple handlers to process log events.
 * It caches the latest 100 log events. If a handler is just added, the
 * handler is able to get access to the cached events and all incoming events.
 * Dev-dashboard and potentially third-party can use it to get access to events.
 */
export default class LogManager {
  private static _handlers: Array<(e: LogEvent) => void> = [];
  private static _events: CircularBuffer<LogEvent> = new CircularBuffer<LogEvent>(LogManager._maxVerboseLog);

  public static addHandler(handler: (e: LogEvent) => void): void {
    if (LogManager._contains(handler)) {
      return;
    }
    LogManager._processAll(handler);
    LogManager._handlers.push(handler);
  }

  public static log(event: LogEvent): void {
    Object.freeze(event);
    LogManager._events.push(event);
    for (const handle of LogManager._handlers) {
      try {
        handle(event);
      } catch (e) {
      console.error('failed to handle event:' + e.toString());
      }
    }
  }

  public static removeHandler(handler: (e: LogEvent) => void): void {
    for (let i: number = 0; i < LogManager._handlers.length; i++) {
      if (LogManager._handlers[i] === handler) {
        LogManager._handlers.splice(i, 1);
        break;
      }
    }
  }

  public static reset(): void {
    LogManager._handlers = [];
    LogManager._events.removeAll();
  }

  private static _contains(handler: (e: LogEvent) => void): boolean {
    for (const existingHandler of LogManager._handlers) {
      if (existingHandler === handler) {
        return true;
      }
    }
    return false;
  }

  private static _processAll(handler: (e: LogEvent) => void): void {
    LogManager._events.forEach(event => {
      if (event) {
        try {
          handler(event);
        } catch (e) {
          console.error('failed to handle event:' + e.toString());
        }
      }
    });
  }

  private static get _maxVerboseLog(): number {
    return 100;
  }
}


