/**
 * LogLevel represents the level of a log event.
 * @internal
 */
export enum LogLevel {
  /**
   * verbose event for debugging
   */
  Verbose = 1,
  /**
   * Informational event
   */
  Info,
  /**
   * A warning
   */
  Warning,
  /**
   * An error
   */
  Error
}


