import { IOAuthUtilityContext, IQosMonitor, ILogger } from '@ms/sp-client-shared';
import QosMonitor from '../telemetry/Api/QosMonitor';
import LogEvent from '../telemetry/Api/LogEvent';
import { LogLevel } from '../telemetry/Api/LogLevel';
import HttpClient, { httpClientServiceKey } from '../httpClient/HttpClient';
import ServiceScope from '../serviceScope/ServiceScope';

class TokenUtilityLogger implements ILogger {
  private static logSource: string = 'TokenUtility';

  public logInfo(message: string): void {
    LogEvent.log(TokenUtilityLogger.logSource, message, LogLevel.Info);
  }

  public logError(message: string): void {
    LogEvent.log(TokenUtilityLogger.logSource, message, LogLevel.Error);
  }

  public logWarning(message: string): void {
    LogEvent.log(TokenUtilityLogger.logSource, message, LogLevel.Warning);
  }
}

/**
 * The DelegationTokenProvider's implementation was moved into the sp-client-shared
 * package so that it can be shared with other teams that are not using sp-client-base
 * platform.  To support this, the sp-client-shared package defines an interface
 * ITokenUtilityContext which abstracts the basic services that are needed by that
 * code.  TokenUtilityContext is our adapter that wires up this interface to the
 * corresponding services in sp-client-base; it can be considered part of the
 * internal implementation of DelegationTokenProvider; it is not  intended to be
 * used by other parts of our framework.
 */
export default class OAuthUtilityContext implements IOAuthUtilityContext {
  private static qosName: string = 'OAuthTokenProvider';
  private _logger: ILogger;

  private _httpClient: HttpClient;

  public get logger(): ILogger {
    return this._logger;
  }

  public constructor(serviceScope: ServiceScope) {
    serviceScope.whenFinished(() => {
      this._httpClient = serviceScope.consume<HttpClient>(httpClientServiceKey);
      this._logger = new TokenUtilityLogger();
    });
  }

  public fetchWithDigest(request: Request): Promise<Response> {
    return request.blob().then<Response>((requestBody: Blob) => {
      return this._httpClient.fetch(request.url, {
        method: request.method,
        headers: request.headers,
        body: requestBody
      });
    });
  }

  public qosMonitor(): IQosMonitor {
    return new QosMonitor(OAuthUtilityContext.qosName);
  }
}


