import { default as ServiceScope } from '../../serviceScope/ServiceScope';
import { default as LogSource, logSourceServiceKey } from './LogSource';
import { LogLevel } from './LogLevel';
import LogManager from './LogManager';

/**
 * An object that represents one log event.
 * @internal
 */
export default class LogEvent {
  private _scope: LogSource;
  private _source: string;
  private _message: string;
  private _error: Error;
  private _level: LogLevel;

  /**
   * Use this method to log an event.
   *
   * @param source - source of the log event, e.g. class name of the source file
   * @param data - data of the log event
   * @param level - level of the log event
   * @param serviceScope - scope of the log event, e.g. component/web part
   */
  public static log(source: string, data: string | Error, level: LogLevel, serviceScope?: ServiceScope): LogEvent {
    const event: LogEvent = new LogEvent(source, data, level, serviceScope);
    LogManager.log(event);
    return event;
  }

  /**
   * Do not call this constructor directly. Use LogEvent.log instead.
   */
  constructor(source: string, data: string | Error, level: LogLevel, serviceScope?: ServiceScope) {
    this._validate(source, data);
    this._scope = this._getScope(serviceScope);
    this._source = source.substr(0, this._maxSourceLength);
    this._setData(data);
    this._level = level;
  }

  public toString(): string {
    let prefix: string = `${this._source}`;
    if (this._scope && !this._scope.isEmpty()) {
      prefix = `${this._scope.id.substr(0, this._maxSourceLength)}.${prefix}`;
    }
    if (this._message) {
      return `[${prefix}] ${this._message}`;
    } else {
      return `[${prefix}] ${this._error.toString()}`;
    }
  }

  private get _maxSourceLength(): number {
    return 20;
  }

  private get _maxMessageLength(): number {
    return 100;
  }

  private _validate(source: string, data: string | Error): void {
    if (!source) {
      throw new Error('source cannot be undefined or null for Logger');
    }
    if (!data) {
      throw new Error('data cannot be undefined or null');
    }
  }

  private _setData(data: string | Error): void {
    if (typeof data === 'string') {
      this._message = data.substr(0, this._maxMessageLength);
    } else if (data instanceof Error) {
      this._error = data;
    }
  }

  private _getScope(serviceScope?: ServiceScope): LogSource {
    let parentSource: LogSource;
    if (serviceScope) {
      serviceScope.whenFinished(() => {
        parentSource = serviceScope.consume(logSourceServiceKey);
      });
    }
    return parentSource;
  }
}


