import * as lodash from '@microsoft/sp-lodash-subset';
import ServiceKey from '../serviceScope/ServiceKey';
import ServiceScope from '../serviceScope/ServiceScope';
import IDigestCache, { digestCacheServiceKey } from './IDigestCache';
import BasicHttpClient, { IBasicHttpClientOptions } from './BasicHttpClient';
import ODataBatch, { IODataBatchOptions } from './ODataBatch';
import HttpClientCommon from './HttpClientCommon';
import { IRandomProvider, randomProviderServiceKey } from '../common/RandomProvider';
import _PerformanceLogger from '../telemetry/Api/PerformanceLogger';
import LogSource, { logSourceServiceKey } from '../telemetry/Api/LogSource';

/**
 * HttpClient is used to perform REST calls against SharePoint.  It adds default
 * headers, manages the digest needed for writes, and collects telemetry that
 * helps the service to monitor the performance of an application.
 *
 * For communicating with non-SharePoint services, use the BasicHttpClient
 * class instead.
 * @public
 */
export default class HttpClient extends BasicHttpClient {
  private static _logSource: LogSource = LogSource.create('httpClient');
  private static _reservedUrlSegments: string[] = [
    '/_API/',
    '/_LAYOUTS/',
    '/_VTI_BIN/'];

  private _digestCache: IDigestCache;
  private _randomProvider: IRandomProvider;
  private _parentSource: LogSource;

  /**
   * This uses a heuristic to guess the SPWeb URL associated with the provided
   * REST URL.  This is necessary for operations such as the X-RequestDigest
   * and ODATA batching, which require POSTing to a separate REST endpoint
   * in order to complete a request.
   * For excample, if the requestUrl is "/sites/site/web/_api/service",
   * the returned URL would be "/sites/site/web".  Or if the requestUrl
   * is "http://example.com/_layouts/service", the returned URL would be
   * "http://example.com".
   * @param requestUrl  The URL for a SharePoint REST service
   * @returns the inferred SPWeb URL
   */
  public static getWebUrlFromRequestUrl(requestUrl: string): string {
    // First we need to guess the webUrl by looking for a reserved
    // segment such as "_api":
    let segmentIndex: number = -1;
    const upperCaseUrl: string = requestUrl.toUpperCase();

    for (const reservedUrlSegment of HttpClient._reservedUrlSegments) {
      segmentIndex = upperCaseUrl.indexOf(reservedUrlSegment);
      if (segmentIndex >= 0) {
        return requestUrl.substr(0, segmentIndex);
      }
    }

    throw new Error('Unable to determine web URL');
  }

  constructor(serviceScope: ServiceScope) {
    super(serviceScope);
    this.serviceScope.whenFinished(() => {
      this._digestCache = serviceScope.consume(digestCacheServiceKey);
      this._randomProvider = serviceScope.consume(randomProviderServiceKey);
      this._parentSource = serviceScope.consume(logSourceServiceKey);
    });
  }

  /**
   * Generally, the parameters and semantics for HttpClient.fetch() are essentially
   * the same as the WHATWG API standard that is documented here:
   * https://fetch.spec.whatwg.org/
   *
   * The HttpClient subclass adds some additional behaviors that are convenient when
   * working with SharePoint ODATA API's (which can be avoided by using
   * BasicHttpClient instead):
   * - Default "Accept" and "Content-Type" headers are added if not explicitly specified.
   * - For write operations, an "X-RequestDigest" header is automatically added
   * - The request digest token is automatically fetched and stored in a cache, with
   *   support for preloading
   *
   * For a write operation, HttpClient will automatically add the "X-RequestDigest"
   * header, which may need to be obtained by issuing a seperate request such as
   * "https://example.com/sites/sample/_api/contextinfo".  Typically the appropriate
   * SPWeb URL can be guessed by looking for a reserved URL segment such as "_api"
   * in the original URL passed to fetch(); if not, use IHttpClientOptions.webUrl
   * to specify it explicitly.
   *
   * @params url - the URL to fetch
   * @params options - additional options that affect the request
   * @returns a promise that will return the result
   * @override
   */
  public fetch(url: string, options: IHttpClientOptions): Promise<Response> {
    if (!options) {
      options = {};
    }

    const modifiedOptions: IHttpClientOptions = lodash.clone(options);

    // Apply default headers
    if (!options.credentials) {
      modifiedOptions.credentials = 'same-origin';
    }

    // NOTE: RequestInit.cache is apparently currently unimplemented in Chrome
    // and our polyfill, so we don't specify it here.

    const headers: Headers = HttpClientCommon.constructDefaultHeaders(options);
    modifiedOptions.headers = headers;

    if (!headers.has('OData-Version')) {
      // SharePoint's guidance is that everyone should be using ODATA version 4.0 now.
      // If you don't provide an OData-Version, then the server will decide using a heuristic,
      // which is not what we want
      headers.append('OData-Version', '4.0');
    }

    // NOTE: If options.method is missing, the default is GET
    if (options.method) {
      const method: string = options.method.toUpperCase();
      if (method !== 'GET' && method !== 'HEAD' && method !== 'OPTIONS') {
        // For writes, SharePoint requires an "X-RequestDigest" header.
        if (!headers.has('X-RequestDigest')) {
          const webUrl: string = options.webUrl || HttpClient.getWebUrlFromRequestUrl(url);

          // Now fetch the digest
          const promise: Promise<Response> = this._digestCache.fetchDigest(webUrl)
            .then((digest: string) => {
              headers.append('X-RequestDigest', digest);

              return this._fetchWithInstrumentation(new Request(url, modifiedOptions));
            });

          return HttpClientCommon.handleAuthenticationErrors(promise, webUrl, this._digestCache);
        }
      }
    }

    return this._fetchWithInstrumentation(new Request(url, modifiedOptions));
  }

  /**
   * Calls fetch(), but sets the method to 'GET'.
   * @params url - the URL to fetch
   * @params options - additional options that affect the request
   * @returns a promise that will return the result
   * @override
   */
  public get(url: string, options?: IHttpClientOptions): Promise<Response> {
    const modifiedOptions: IHttpClientOptions = lodash.clone(options || {});
    modifiedOptions.method = 'GET';
    return this.fetch(url, modifiedOptions);
  }

  /**
   * Calls fetch(), but sets the method to 'POST'.
   * @params url - the URL to fetch
   * @params options - additional options that affect the request
   * @returns a promise that will return the result
   * @override
   */
  public post(url: string, options: IHttpClientOptions): Promise<Response> {
    const modifiedOptions: IHttpClientOptions = lodash.clone(options || {});
    modifiedOptions.method = 'POST';
    return this.fetch(url, modifiedOptions);
  }

  /**
   * Begins an ODATA batch, which allows multiple REST queries to be bundled into
   * a single web request.
   */
  public beginBatch(batchOptions?: IODataBatchOptions): ODataBatch {
    return new ODataBatch(this.serviceScope, batchOptions);
  }

  private _fetchWithInstrumentation(request: Request): Promise<Response> {
    const tag: string = this._performanceTag;
    _PerformanceLogger.startMarkForApi(tag);
    return this.fetchCore(request).then((response: Response) => {
      _PerformanceLogger.endMarkForApi(tag, this._logSourceId, request.url, response);
      return response;
    });
  }

  private get _performanceTag(): string {
    const randomSuffix: string = Math.floor(this._randomProvider.getRandom() * 100000).toString();
    return `${this._logSourceId}.${randomSuffix}`;
  }

  private get _logSourceId(): string {
    if (this._parentSource.isEmpty()) {
      return HttpClient._logSource.id;
    }
    return this._parentSource.id;
  }
}

export const httpClientServiceKey: ServiceKey<HttpClient> = ServiceKey.create<HttpClient>
  ('sp-client-base:HttpClient', HttpClient);

/**
 * This interface defines the options for the HttpClient operations such as
 * get(), post(), fetch(), etc.  It is based on the WHATWG API standard
 * parameters that are documented here:
 * https://fetch.spec.whatwg.org/
 * @public
 */
export interface IHttpClientOptions extends IBasicHttpClientOptions {
  /**
   * For a write operation, HttpClient will automatically add the
   * "X-RequestDigest" header, which may need to be fetched using a seperate
   * request such as "https://example.com/sites/sample/_api/contextinfo".
   * Typically the SPWeb URL ("https://example.com/sites/sample" in this
   * example) can be guessed by looking for a reserved URL segment such
   * as "_api" in the original REST query, however certain REST endpoints
   * do not contain a reserved URL segment; in this case, the webUrl can
   * be explicitly specified using this option.
   */
  webUrl?: string;
}



