import {
  IBeaconHandlers,
  IClonedEvent,
  ClonedEventType as ClonedEventTypeEnum,
  Nav as NavEvent,
  PLTHttpRequest as PLTHttpRequestEvent,
  ResultTypeEnum
} from '@ms/odsp-utilities-bundle';

class BeaconHandlers implements IBeaconHandlers {
  private static _getResultTypeSuffix(resultType: ResultTypeEnum): string {
    'use strict';
    if (resultType === ResultTypeEnum.Success) {
      return '.Success';
    } else if (resultType === ResultTypeEnum.Failure) {
      return '.Failure';
    } else if (resultType === ResultTypeEnum.ExpectedFailure) {
      return '.ExpectedFailure';
    }

    return '';
  }

  // ignore these events, or they will show up under UnknownEvent
  public ignoredEventsHandler(event: IClonedEvent): boolean {
    return NavEvent.isTypeOf(event) || PLTHttpRequestEvent.isTypeOf(event);
  }

  public qosEventNameHandler(event: IClonedEvent, currentName: string): string {
    let name: string = currentName;
    if (event.data) {
      if (event.eventType === ClonedEventTypeEnum.Start) {
        name += '.Start';
      } else if (event.eventType === ClonedEventTypeEnum.End) {
        if (event.data.resultCode) {
          name += '.' + event.data.resultCode;
        }
        name += BeaconHandlers._getResultTypeSuffix(event.data.resultType);
      } else {
        name += '.no_EventType';
      }
    }

    return name;
  }

  /* tslint:disable:no-any */
  public qosEventExtraDataHandler(event: IClonedEvent, qosData: any): void {
    return;
  }
  /* tslint:enable:no-any */

}

export default BeaconHandlers



