import ServiceScope from '../serviceScope/ServiceScope';
import ServiceKey from '../serviceScope/ServiceKey';

/**
 * This interface allows unit tests to provide a deterministic
 * source of pseudorandom numbers.
 */
export interface IRandomProvider {
  /**
   * Returns a psuedorandom number between 0 (inclusive) and 1 (exclusive),
   * following the contract of Math.random().
   */
  getRandom(): number;
}

/**
 * This is the default implementation of IRandomProvider that simply
 * calls Math.random().
 */
export default class RandomProvider implements IRandomProvider {
  constructor(serviceScope: ServiceScope) {
    // (this constructor is currently unused, but it is required by the
    // ServiceKey.create() contract)
  }

  public getRandom(): number {
    return Math.random();
  }
}

export const randomProviderServiceKey: ServiceKey<IRandomProvider>
  = ServiceKey.create<IRandomProvider>('sp-client-base:RandomProvider', RandomProvider);



