import ServiceScope from '../serviceScope/ServiceScope';
import UrlUtilities from '../common/UrlUtilities';
import { IFetchProvider, fetchProviderServiceKey } from './FetchProvider';
import { ITimeProvider, dateProviderServiceKey } from '../common/TimeProvider';
import IDigestCache from './IDigestCache';
import LogSource from '../telemetry/Api/LogSource';
import TraceLogger from '../telemetry/Api/TraceLogger';

/**
 * The internal implementation of IDigestCache.
 */
export default class DigestCache implements IDigestCache {
  // The digest cache expiration times are measured from whenever the C# code on the server
  // generated the digest, whereas in the current implementation, we start measuring from
  // whenever the framework finished loading.  We do have access to the server's timestamp,
  // relying on it could run into trouble if the browser's clock is wrong.  SPPPlat VSO 223612
  // will add a bit of logic to TimeProvider to protect against that.  Until then, we can
  // work around the issue by trimming the expiration times back by a small amount, which
  // represents the estimated page render time.
  private static EXPIRATION_SLOP_MS: number = 3000;

  private static _logSource: LogSource = LogSource.create('DigestCache');
  private _fetchProvider: IFetchProvider;
  private _dateProvider: ITimeProvider;

  private _digestsByUrl: Map<string, ICachedDigest>;

  constructor(serviceScope: ServiceScope) {
    this._digestsByUrl = new Map<string, ICachedDigest>();

    serviceScope.whenFinished(() => {
      this._fetchProvider = serviceScope.consume(fetchProviderServiceKey);
      this._dateProvider = serviceScope.consume(dateProviderServiceKey);
    });
  }

  /** Implements IDigestCache */
  public fetchDigest(webUrl: string): Promise<string> {
    const normalizedWebUrl: string = UrlUtilities.removeEndSlash(webUrl);
    const cachedDigest: ICachedDigest = this._digestsByUrl.get(normalizedWebUrl);

    if (cachedDigest) {
      const timestamp: number = this._dateProvider.getTimestamp();
      if (timestamp < cachedDigest.expirationTimestamp) {
        TraceLogger.logVerbose(DigestCache._logSource,
          'DigestCache: Reusing cached digest.  Expiration: ' + cachedDigest.expirationTimestamp);
        return Promise.resolve(cachedDigest.value);
      }
    }

    // If it wasn't in the cache, then we need to fetch a new one.
    // Create the REST API URL
    const digestUrl: string = normalizedWebUrl + '/_api/contextinfo';

    // NOTE: We don't want people to be injecting random headers throughout
    // their code.  As HttpClient evolves, maybe there is some way we can
    // combine this with the headers being added there.
    const rawRequest: Request = new Request(digestUrl,
      {
        method: 'POST',
        headers: {
          'Accept': 'application/json',
          'Content-type': 'application/json;odata=verbose;charset=utf-8'
        },
        credentials: 'same-origin',
        cache: 'no-cache'
      });

    return this._fetchProvider.fetch(rawRequest).then((response: Response) => {
      return response.json();
    }).then((responseObject: IRestResponse) => {

      const digestValue: string = responseObject.FormDigestValue;
      const seconds: number = responseObject.FormDigestTimeoutSeconds as number;
      const expirationTimeMs: number = this._dateProvider.getTimestamp() + 1000 * seconds;

      this.addDigestToCache(normalizedWebUrl, digestValue, expirationTimeMs);

      TraceLogger.logVerbose(DigestCache._logSource, 'DigestCache: Fetched new digest');

      return digestValue;
    });
  }

  /** Implements IDigestCache */
  public addDigestToCache(webUrl: string, digestValue: string, expirationTimestamp: number): void {
    const normalizedWebUrl: string = UrlUtilities.removeEndSlash(webUrl);
    const newCachedDigest: ICachedDigest = {
      value: digestValue,
      expirationTimestamp: expirationTimestamp - DigestCache.EXPIRATION_SLOP_MS
    };
    this._digestsByUrl.set(normalizedWebUrl, newCachedDigest);
  }

  /** Implements IDigestCache */
  public clearDigest(webUrl: string): boolean {
    const normalizedWebUrl: string = UrlUtilities.removeEndSlash(webUrl);
    const found: boolean = this._digestsByUrl.delete(normalizedWebUrl);
    TraceLogger.logVerbose(DigestCache._logSource,
      `DigestCache: Requested to clear cache entry: ${found ? 'found' : 'not found'}`);
    return found;
  }

  /** Implements IDigestCache */
  public clearAllDigests(): void {
    this._digestsByUrl.clear();
  }
}

/**
 * Used internally by DigestCache.  This stores a request digest value and
 * its expiration date.
 */
interface ICachedDigest {
  /**
   * The string representing the digest.  Example value:
   * "0x6CB1C55E8C18B86...,20 Feb 2016 00:18:17 -0000"
   */
  value: string;

  /**
   * The expiration time for the digest, in milliseconds measured using DateTime.now()
   */
  expirationTimestamp: number;
}

/**
 * Represents the JSON response of the /_api/contextinfo REST call.
 */
interface IRestResponse {
  FormDigestValue: string;
  FormDigestTimeoutSeconds: number;
}



