/**
 * This file was copied from
 * https://onedrive.visualstudio.com/OneDriveWeb/_git/odsp-utilities?path=%2Fsrc%2Flocal%2Faria%2FAriaLogger.ts
 * odsp has some special requirejs configuration to indicate how the aria is loaded and
 * that config comes from server side. We use exports loader to tell webpack how to load aria.
 * To make life easier, we choose to copy this file instead of complicated loading logic.
 * Some refactoring was done to make the code better, but the logic is the same.
 */

import { ClonedEventType as ClonedEventTypeEnum } from '@ms/odsp-utilities/lib/logging/EventBase';
import { default as IClonedEvent } from '@ms/odsp-utilities/lib/logging/IClonedEvent';
import { Manager } from '@ms/odsp-utilities/lib/logging/Manager';
import { IBeaconStartSchema } from '@ms/odsp-utilities/lib/logging/events/Beacon.event';
import { default as BeforeUnload } from '@ms/odsp-utilities/lib/beforeUnload/BeforeUnload';
import { default as BeaconEvent } from '@ms/odsp-utilities/lib/logging/events/Beacon.event';
import { default as ErrorHelper } from '@ms/odsp-utilities/lib/logging/ErrorHelper';
import { default as PlatformDetection } from '@ms/odsp-utilities/lib/browser/PlatformDetection';
import { IEventMetadata } from '@ms/odsp-utilities/lib/logging/IEvent';

const ARIA_QOS_NAME: string = 'AriaBeacon';

export interface IPageContextData {
    isAuthenticated: boolean;
    market: string;
    userId: string;
    userMsaId?: string;
    userANID?: string;
    version: string;
    manifest: string;
    session: string;
    environment?: string;
    workload?: string;
    siteSubscriptionId?: string;
    farmLabel?: string;
}

export default class AriaLogger {
    private static _ariaTelemtry: typeof microsoft.applications.telemetry;
    private static _logger: microsoft.applications.telemetry.Logger;

  public static initialize(
    tenantToken: string,
    context: IPageContextData): void {
    require.ensure(['exports?microsoft.applications.telemetry!@ms/aria-private'],
    (require: (path: string) => typeof microsoft.applications.telemetry) => {
      const aria: typeof microsoft.applications.telemetry =
        require('exports?microsoft.applications.telemetry!@ms/aria-private');
      try {
        AriaLogger._ariaTelemtry = aria;
        AriaLogger._ariaTelemtry.LogManager.initialize(tenantToken);
        AriaLogger._logger = new AriaLogger._ariaTelemtry.Logger();
        AriaLogger.setContext(context);
        AriaLogger.registerUnloadFlush();
        AriaLogger.registerBeacon();

        const missedClonedEvents: IClonedEvent[] = Manager.addLogHandler((event: IClonedEvent) => {
            AriaLogger.logEvent(event);
        });

        for (const event of missedClonedEvents) {
            AriaLogger.logEvent(event);
        }
      } catch (e) {
        if (e instanceof AriaLogger._ariaTelemtry.Exception) {
            e = new Error(`Aria error: ${e.toString()}`);
        }
        ErrorHelper.log(e);
      }
    },
    'sp-client-telemetry');
  }

  private static setContext(context: IPageContextData): void {
    const platformDetection: PlatformDetection = new PlatformDetection();
    const semanticContext: microsoft.applications.telemetry.ISemanticContext =
      AriaLogger._logger.getSemanticContext();
    /* tslint:disable:no-string-literal no-any */
    // This is apparently a hack to add more context info, due to aria's lack of
    // extensibility.
    const contextMap: any = semanticContext['contextMap'];
    /* tslint:enable:no-string-literal no-any */

    contextMap.Add('AppInfo.Session', context.session);
    semanticContext.setAppVersion(context.version);
    contextMap.Add('AppInfo.Manifest', context.manifest);

    semanticContext.setUserLanguage(context.market || '');
    semanticContext.setUserId(context.userId);
    semanticContext.setUserMsaId(context.userMsaId);
    semanticContext.setUserANID(context.userANID);
    AriaLogger._logger.setContext('Environment', context.environment);
    AriaLogger._logger.setContext('Workload', context.workload);
    AriaLogger._logger.setContext('IsAuthenticated', context.isAuthenticated ? 1 : 0);

    semanticContext.setDeviceOsName(platformDetection.osName);
    semanticContext.setDeviceOsVersion(platformDetection.osVersion);
    AriaLogger._logger.setContext('BrowserName', platformDetection.browserName);
    AriaLogger._logger.setContext('BrowserMajVer', platformDetection.browserMajor);
    AriaLogger._logger.setContext('BrowserMinVer', platformDetection.browserMinor);
    AriaLogger._logger.setContext('BrowserUserAgent', platformDetection.userAgent);
    AriaLogger._logger.setContext('BrowserIsMobile', platformDetection.isMobile);

    if (context.farmLabel) {
        AriaLogger._logger.setContext('FarmLabel', context.farmLabel);
    }

    if (context.siteSubscriptionId) {
        AriaLogger._logger.setContext('SiteSubscriptionId', context.siteSubscriptionId);
    }
  }

  private static registerUnloadFlush(): void {
    /* tslint:disable:no-null-keyword */
    // null check is used in aria.js
    BeforeUnload.init();
    BeforeUnload.registerHandler((unload: boolean): string => {
        if (unload) {
            AriaLogger._ariaTelemtry.LogManager.flush(null);
        }
        return null;
    });
    /* tslint:enable:no-null-keyword */
  }

  private static registerBeacon(): void {
    // Listen to aria beaconing and send qos events to monitor its success rate
    AriaLogger._ariaTelemtry.LogManager.addCallbackListener((
      isSuccess: number,
      statusCode: number,
      tenantToken: string,
      events: microsoft.applications.telemetry.datamodels.Record[]) => {
      const beaconEvent: BeaconEvent = new BeaconEvent({
        name: ARIA_QOS_NAME,
        retryCount: 0,
        totalRetries: 0,
        eventCount: events ? events.length : 0
      });

      beaconEvent.end({
        success: isSuccess === 0,
        status: statusCode + ''
      });

      if (DEBUG) {
        // Display errors if the aria logger is failing to log
        if (isSuccess !== 0) {
            ErrorHelper.log(new Error(`Aria logger failed with status code ${statusCode}`));
        }
      }
    });
  }

  private static logEvent(event: IClonedEvent): void {
    // Dont log its self qos event
    if (BeaconEvent.isTypeOf(event) && event.data) {
        const data: IBeaconStartSchema = event.data;
        if (data.name === ARIA_QOS_NAME) {
            return;
        }
    }

    if (event.enabled) {
      const eventProperties: microsoft.applications.telemetry.EventProperties =
        new AriaLogger._ariaTelemtry.EventProperties();

      eventProperties.setProperty('Vector', event.vector.toString());
      eventProperties.setProperty('ValidationErrors', event.validationErrors);

      const splitEventName: string[] = event.eventName.split(',');
      const eventName: string = `ev_${splitEventName[splitEventName.length - 2]}`;
      const fullEventName: string = `${event.eventName}`;

      if (event.eventType === ClonedEventTypeEnum.End) {
          eventProperties.setProperty('Duration', event.endTime - event.startTime);
      }
      /* tslint:disable:no-any forin */
      const data: any = event.data;

      if (data) {
        for (const x in data) {
          const propertyMetadata: IEventMetadata = event.metadata[x];

          if (propertyMetadata) {
            const loggingName: string = `${propertyMetadata.definedInName}_${x}`;
            const value: any = data[x];

            if (propertyMetadata.isMetric) {
              if (value !== undefined) {
                  eventProperties.setProperty(loggingName, value);
              }
            } else {
              if (propertyMetadata.baseType === 'Enum') {
                eventProperties.setProperty(loggingName, propertyMetadata.typeRef[value]);
              } else if (propertyMetadata.type === 'Object') {
                const dataObject: any = value;
                for (const y in dataObject) {
                    eventProperties.setProperty(`${loggingName}_${y.replace('.', '_')}`, dataObject[y]);
                }
              } else {
                eventProperties.setProperty(loggingName, value);
              }
            }
          }
        }
      }
      /* tslint:enable:no-any forin */
      eventProperties.name = eventName;
      eventProperties.setProperty('WebLog_FullName', fullEventName);
      eventProperties.setProperty('WebLog_EventType', ClonedEventTypeEnum[event.eventType]);

      for (const name of splitEventName) {
        if (name) {
            eventProperties.setProperty(`WebLog_Type_${name}`, 1);
        }
      }

      AriaLogger._logger.logEvent(eventProperties);
    }
  }
}


