import ServiceScope from '../serviceScope/ServiceScope';
import ServiceKey from '../serviceScope/ServiceKey';

/**
 * This interface represents the basic contract of the ES6 Fetch API, without
 * any additional SharePoint logic.
 * @see {@link https://developer.mozilla.org/en-US/docs/Web/API/Fetch_API }
 *
 * @public
 */
export interface IFetchProvider {
  /**
   * The parameters and semantics for this function are the same as the
   * ES6 GlobalFetch.fetch() standard.
   * @see {@link https://developer.mozilla.org/en-US/docs/Web/API/Fetch_API }
   */
  fetch(request: Request): Promise<Response>;
}

/**
 * The default implementation of IFetchProvider, which simply calls window.fetch().
 */
export default class FetchProvider implements IFetchProvider {
  constructor(serviceScope: ServiceScope) {
    // (this constructor is currently unused, but it is required by the
    // ServiceKey.create() contract)
  }

  public fetch(request: Request): Promise<Response> {
    return window.fetch(request);
  }
}

/**
 * The service key for IFetchProvider.
 * @public
 */
export const fetchProviderServiceKey: ServiceKey<IFetchProvider>
  = ServiceKey.create<IFetchProvider>('sp-client-base:FetchProvider', FetchProvider);



