import { IOAuthToken } from './IOAuthToken';
import Constants from './Constants';
import { IOAuthUtilityContext, IQosMonitor } from './IOAuthUtilityContext';
import {
  IResponseBody,
  IServerError,
  IServerErrorResponse,
  ITokenResponseData,
  ITokenRequestData
} from './TokenRequestDataDefinitions';
import OAuthUtilityError, { OAuthUtilityErrorType } from './OAuthUtilityError';

/**
 * The TokenUtility class facilitates getting a OAuth token for a given resource.
 * It handles caching and token expiration in a way that is transparent to the library user.
 *
 * @alpha
 */
export default class OAuthUtility {
  private _context: IOAuthUtilityContext;

  public constructor(context: IOAuthUtilityContext) {
    this._context = context;
  }

  /**
   * Fetches the OAuth token for a resource if the user that's currently logged in has
   * access to that resource.
   *
   * If the promise returned by this method rejects, there is no point in retrying the same call.
   * If the response from the server signals that the request should be retried this function will do this by itself.
   *
   * An example of a resource would be https://sdfpilot.outlook.com
   *
   * @param resource - the resource for which the token should be obtained
   * @returns A promise that will be fullfiled with the token or that will reject
   *          with an instance of TokenUtilityError
   */
  public getOAuthToken(resource: string): Promise<IOAuthToken> {
    const cachedData: ITokenResponseData = this._getCachedTokenData(resource);

    if (cachedData && !this._isExpiring(cachedData)) {
      return Promise.resolve<IOAuthToken>({
        token: cachedData.accessToken,
        isGuest: false
      });
    }

    return this._retriableFetchWithDigest(resource, Constants.retries)
      .then<IOAuthToken>((responseData: ITokenResponseData) => {
        this._storeTokenData(resource, responseData);
        return {
          token: responseData.accessToken,
          isGuest: false
        };
      });
  }

  /**
   * Clears the token from a given resource from the internal cache. If there is no token
   * cached for the resource the method does nothing.
   *
   * @param resource - resource for which the token should be cleared from the cache
   **/
  public clearCachedToken(resource: string): void {
    window.sessionStorage.removeItem(this._getCacheKey(resource));
  }

  /**
   * @param resource - the resource for which the request is done
   * @param retriesLeft - number of retries to be done in case of a retriable error
   * @returns A promise that fullfills to the server response or rejects with an instance of TokenUtilityError
   */
  private _retriableFetchWithDigest(resource: string, retriesLeft: number): Promise<ITokenResponseData> {
    const monitor: IQosMonitor = this._context.qosMonitor();
    return this._context.fetchWithDigest(this._buildTokenRequest(resource))
      .then<ITokenResponseData>((response: Response) => {
        if (response.status !== 200) {
          /**
           * If the request code is not 400 this means that the error is not raised by the rest API.
           * In this case, the error is passed directly to the user.
           *
           * @TODO #225093
           * Throttling response will have code 429. This also needs to be handled separately
           */
          this._context.logger.logError('fetchWithDigest failed. Server responded with ' + response.statusText);
          const error: OAuthUtilityError = new OAuthUtilityError(OAuthUtilityErrorType.unknownError, {
              httpResponseStatus: response.status,
              message: response.statusText
          });
          if (response.status !== 400) {
            monitor.writeUnexpectedFailure('UnknownError', error);
            return Promise.reject(error);
          }
          return response.json<IServerErrorResponse>()
            .then<ITokenResponseData>((errorResponse: IServerErrorResponse) => {
              const errorCode: number = this._getErrorCodeFromResponseCode(errorResponse.error.code);
              if (!this._isRetriableError(errorCode) || (retriesLeft === 0)) {
                const error: OAuthUtilityError = this._buildErrorFromResponse(errorResponse);
                monitor.writeUnexpectedFailure('SharepointServerError', error);
                return Promise.reject(error);
              }
              this._context.logger.logError('fetchWithDigest failed. Retrying. Retries left: ' + retriesLeft);
              return this._retriableFetchWithDigest(resource, retriesLeft - 1);
            });
        }
        return this._instrumentedParseResponseData(response, monitor);
      });
  }

  private _getCachedTokenData(resource: string): ITokenResponseData {
    return JSON.parse(window.sessionStorage.getItem(this._getCacheKey(resource)));
  }

  private _storeTokenData(resource: string, data: ITokenRequestData): void {
    window.sessionStorage.setItem(this._getCacheKey(resource), JSON.stringify(data));
  }

  private _getCacheKey(resource: string): string {
    return ['sp-client-shared', Constants.npmPackageVersion, 'OAuthUtility', resource].join(';');
  }

  private _isExpiring(tokenRequestData: ITokenResponseData): boolean {
    return (tokenRequestData.expiresOn - Date.now() / 1000) < Constants.aboutToExpireIntervalSeconds;
  }

  private _isRetriableError(errorCode: number): boolean {
    return Constants.retriableErrorCodes.indexOf(errorCode) !== -1;
  }

  private _buildTokenRequest(resource: string): Request {
    const requestHeaders: Headers = new Headers();
    requestHeaders.append('Content-Type', 'application/json; charset=utf-8');
    requestHeaders.append('Odata-Version', '4.0');
    requestHeaders.append('Accept', 'application/json;odata.metadata=minimal');

    const request: RequestInit = {
      method: 'POST',
      headers: requestHeaders,
      body: JSON.stringify(this._buildRequestBody(resource))
    };

    return new Request(Constants.apiUrl, request);
  }

  private _buildRequestBody(resource: string): ITokenRequestData {
    return {
      resource: resource
    };
  }

  /**
   * The returned error contains the same message as the server error and a numeric error code
   * coresponding to the error code sent by the server
   *
   * @param serverErrorResponse - The error response object as it is sent by the server
   * @returns An instance of TokenUtilityError
   **/
  private _buildErrorFromResponse(serverErrorResponse: IServerErrorResponse): OAuthUtilityError {
    const serverError: IServerError = serverErrorResponse.error;
    const errorCode: number = this._getErrorCodeFromResponseCode(serverError.code);
    if (errorCode === undefined) {
      return new OAuthUtilityError(OAuthUtilityErrorType.malformedResponseBody, {
        httpResponseStatus: 400,
        message: Constants.unexpectedErrorCodeFormat
      });
    }
    return new OAuthUtilityError(OAuthUtilityErrorType.serverError, {
      httpResponseStatus: 400,
      message: serverError.message,
      serverErrorCode: errorCode
    });
  }

  /**
   * The format is <errorCode>, <exception class name>
   *
   * @param responseErrorCode - The response error code as it is sent by the server
   */
  private _getErrorCodeFromResponseCode(responseErrorCode: string): number {
    const firstCommaIndex: number = responseErrorCode.indexOf(',');
    if (firstCommaIndex === -1) {
      return undefined;
    }
    return Number(responseErrorCode.substr(0, firstCommaIndex));
  }

  private _instrumentedParseResponseData(response: Response, monitor: IQosMonitor): Promise<ITokenResponseData> {
    try {
      return response.json().then<ITokenResponseData>((responseBody: IResponseBody) => {
        monitor.writeSuccess();
        return {
          accessToken: responseBody.access_token,
          expiresOn: Number(responseBody.expires_on),
          notBefore: Number(responseBody.not_before),
          resource: responseBody.resource,
          tokenType: responseBody.token_type
        };
      });
    } catch (error) {
      monitor.writeUnexpectedFailure('ResponseParseError', error);
      return Promise.reject(
        new OAuthUtilityError(OAuthUtilityErrorType.malformedResponseBody, {
          httpResponseStatus: 200,
          message: Constants.unexpectedTokenMessageFormat
        })
      );
    }
  }
}


