/**
 * @copyright Microsoft Corporation. All rights reserved.
 * @file QosLogger.ts
 * Qos logger
 * This class represents the Qos monitor APIs
 */
import { Qos as QosEvent } from '@ms/odsp-utilities-bundle';
import { ResultTypeEnum as QosResultEnum } from '@ms/odsp-utilities-bundle';
import IQosMonitor from './IQosMonitor';

/**
 * @internal
 */
export default class QosMonitor implements IQosMonitor {
  // @todo VSO:237921 - use the correct typings
  public qos: any; // tslint:disable-line

   /**
  * Creates a QoS monitor (initializes it and logs the start event)
  * @param	scenarioName	Unique name of the QoS scenario
  * You will see start tag: ModernPublish.RenderPublishPage.Start
  */
  constructor(scenarioName: string) {
    if (!scenarioName || scenarioName.length === 0) {
      throw new Error('senanrio name is invalid');
    }

    this.qos = new QosEvent({ name: scenarioName });
  }

  /**
   * QoS monitor ends with success
   * You will see success tag: ModernPublish.RenderPublishPage.Success
   */
  public writeSuccess(): void {
    if (!this.qos) {
      throw new Error ('monitor instance is null or undefined');
    }

    this.qos.end({ resultType: QosResultEnum.Success });
  }

  /**
   * QoS monitor ends with failure
   * You will see success tag: ModernPublish.RenderPublishPage.<failuretag>.Failure.
   * <failuretag> will be filled with @param tagName
   */
  public writeUnexpectedFailure(
    tagNameSuffix: string = 'DefaultUnexpected',
    ex?: Error,
    /* tslint:disable:no-any */
    extraData?: { [key: string]: any }
    /* tslint:enable:no-any */ ): void {
    if (!this.qos) {
      throw new Error ('monitor instance is null or undefined');
    }

    if (ex) {
      this.qos.end({
        resultCode: tagNameSuffix,
        resultType: QosResultEnum.Failure,
        error: ex.message,
        extraData: extraData
      });
    } else {
      this.qos.end({
        resultCode: tagNameSuffix,
        resultType: QosResultEnum.Failure,
        extraData: extraData
      });
    }
  }

  /*
   * QoS monitor ends with expected failure
   * You will see success tag: ModernPublish.RenderPublishPage.<failuretag>.ExpectedFailure.
   * <failuretag> will be filled with @param tagName
   */
  public writeExpectedFailure(
    tagNameSuffix: string = 'DefaultExpected',
    ex?: Error,
    /* tslint:disable:no-any */
    extraData?: { [key: string]: any }
    /* tslint:enable:no-any */ ): void {
    if (!this.qos) {
      throw new Error ('monitor instance is null or undefined');
    }
    if (ex) {
      this.qos.end({
        resultCode: tagNameSuffix,
        resultType: QosResultEnum.ExpectedFailure,
        error: ex.message,
        extraData: extraData
      });
    } else {
      this.qos.end({
        resultCode: tagNameSuffix,
        resultType: QosResultEnum.ExpectedFailure,
        extraData: extraData
      });
    }
  }
}



