export type ODataBatchedRequestResolveCallback = (value?: Response) => void;
/* tslint:disable:no-any */
export type ODataBatchedRequestRejectCallback = (value?: any) => void;
/* tslint:enable:no-any */

/**
 * INTERNAL USAGE ONLY
 * This is a helper class used by ODataBatch to track the state of each
 * queued REST request.
 */
export default class ODataBatchedRequest {
  public request: Request;
  public response: Response;

  public promise: Promise<Response>;

  private _queuedPromiseResolves: ODataBatchedRequestResolveCallback[];
  private _queuedPromiseRejects: ODataBatchedRequestRejectCallback[];

  constructor(request: Request) {
    this.request = request;

    this.response = undefined;
    this._queuedPromiseResolves = [];
    this._queuedPromiseRejects = [];

    // Create the promise for this request.
    this.promise = new Promise<Response>(
      /* tslint:disable:no-any */
      (resolve: (value?: Response) => void, reject: (error?: any) => void) => {
      /* tslint:enable:no-any */
        if (this.response !== undefined) {
          // If someone calls .then() after the promise is fulfilled, then
          // immediately return the response
          resolve(this.response);
        } else {
          // If someone calls .then() before the batch has executed or finished,
          // we add them to a queue that will be fulfilled later.
          this._queuedPromiseResolves.push(resolve);
          this._queuedPromiseRejects.push(reject);
        }
      }
    );
  }

  /**
   * The ODataBatch class calls this method while parsing the HTTP response for
   * the batch.  If there are any queued promises, this will resolve or reject
   * them as appropriate.
   *
   * NOTE: This function is only ever called once, because a BATCH can only
   * be executed once.
   */
  public notifyCompleted(response: Response): void {
    // Record the response
    this.response = response;

    // Fulfill the promises
    if (response.ok) {
      this._queuedPromiseResolves.forEach(
        (callback: ODataBatchedRequestResolveCallback) => {
          callback(this.response);
        }
      );
    } else {
      const error: Error = new Error('ODataBatch: Batched request failed');
      this._queuedPromiseRejects.forEach(
        (callback: ODataBatchedRequestRejectCallback) => {
          callback(error);
        }
      );
    }

    this._queuedPromiseResolves = [];
    this._queuedPromiseRejects = [];
  }
}



