import * as lodash from '@microsoft/sp-lodash-subset';
import ServiceScope from '../serviceScope/ServiceScope';
import ODataBatchedRequest from './ODataBatchedRequest';
import HttpClient from './HttpClient';
import Guid from '../guid/Guid';
import { IFetchProvider, fetchProviderServiceKey  } from './FetchProvider';
import { IRandomProvider, randomProviderServiceKey } from '../common/RandomProvider';
import IDigestCache, { digestCacheServiceKey } from './IDigestCache';
import HttpClientCommon from './HttpClientCommon';

/**
 * The ODataBatch class accumulates a number of REST service calls and
 * transmits them as a single ODATA batch.  This protocol is documented here:
 * http://docs.oasis-open.org/odata/odata/v4.0/odata-v4.0-part1-protocol.html
 *
 * The usage is to call ODataBatch.fetch() to queue each individual request,
 * and then call ODataBatch.execute() to execute the batch operation.
 * The execute() method returns a promise that resolves when the real REST
 * call has completed.  Each call to fetch() also returns a promise that will
 * resolve with a Response object for that particular request.
 *
 * @public
 */
export default class ODataBatch {
  private _batchOptions: IODataBatchOptions;
  private _fetchProvider: IFetchProvider;
  private _randomProvider: IRandomProvider;
  private _digestCache: IDigestCache;

  private _batchedRequests: ODataBatchedRequest[];
  private _webUrl: string;

  public constructor(serviceScope: ServiceScope, batchOptions?: IODataBatchOptions) {
    this._batchOptions = batchOptions || {};
    this._batchedRequests = [];

    serviceScope.whenFinished(() => {
      this._fetchProvider = serviceScope.consume(fetchProviderServiceKey);
      this._randomProvider = serviceScope.consume(randomProviderServiceKey);
      this._digestCache = serviceScope.consume(digestCacheServiceKey);
    });
  }

  /**
   * Queues a new request, and returns a promise that can be used to access
   * the server response (after execute() has completed).  The parameters for
   * this function are basically the same as the WHATWG API standard documented here:
   * https://fetch.spec.whatwg.org/
   *
   * However, be aware that certain REST headers are ignored or not allowed inside
   * a batch.  See the ODATA documentation for details.
   *
   * When execute() is called, it will POST to a URL such as
   * "http://example.com/sites/sample/_api/$batch".  Typically ODataBatch can successfully
   * guess the appropriate SPWeb URL by looking for a reserved URL segment such as "_api"
   * in the first URL passed to fetch().  If not, use IODataBatchOptions.webUrl to specify it
   * explicitly.
   *
   * @params url - the URL to fetch, or an already initialized Request object
   * @params options - additional options that affect the request
   * @returns a promise that will return the result
   */
  public fetch(url: string, options?: IODataBatchRequestOptions):
    Promise<Response> {

    if (!options) {
      options = {};
    }

    // Apply most of the same default headers as HttpClient
    const modifiedOptions: IODataBatchRequestOptions = lodash.clone(options);
    modifiedOptions.headers = HttpClientCommon.constructDefaultHeaders(options);

    const request: Request = new Request(url, modifiedOptions);
    const requestUrl: string = request.url;

    if (!this._webUrl) {
      // If the webUrl wasn't provided to the constructor, then we infer it from
      // the first request.
      this._webUrl = HttpClient.getWebUrlFromRequestUrl(requestUrl);
    }

    const batchedRequest: ODataBatchedRequest = new ODataBatchedRequest(request);
    this._batchedRequests.push(batchedRequest);
    return batchedRequest.promise;
  }

  /**
   * Calls fetch(), but sets the method to 'GET'.
   * @params url - the URL to fetch
   * @params options - additional options that affect the request
   * @returns a promise that will return the result
   */
  public get(url: string, options?: IODataBatchRequestOptions): Promise<Response> {
    const modifiedOptions: IODataBatchRequestOptions = lodash.clone(options || {});
    modifiedOptions.method = 'GET';
    return this.fetch(url, modifiedOptions);
  }

  /**
   * Calls fetch(), but sets the method to 'POST'.
   * @params url - the URL to fetch
   * @params options - additional options that affect the request
   * @returns a promise that will return the result
   */
  public post(url: string, options: IODataBatchRequestOptions): Promise<Response> {
    const modifiedOptions: IODataBatchRequestOptions = lodash.clone(options || {});
    modifiedOptions.method = 'POST';
    return this.fetch(url, modifiedOptions);
  }

  /**
   * Executes the batched queries that were queued using ODataBatch.fetch().
   */
  public execute(): Promise<ODataBatch> {
    let batchBody: string = '\n';

    const batchGuid: Guid = Guid.newGuid(this._randomProvider);

    // In the loop below, we will build up a chain of promises that append
    // each request to the batch body, and then the "return" statement will
    // add the final processing to the chain.
    let chain: Promise<void> = Promise.resolve<void>();

    this._batchedRequests.forEach(
      (batchedRequest: ODataBatchedRequest) => {

        chain = chain.then(() => {
          return batchedRequest.request.text();
        }).then((requestText: string) => {
          const request: Request = batchedRequest.request;

          batchBody += `--batch_${batchGuid.toString()}\n`;
          batchBody += `Content-type: application/http\n`
            + `Content-Transfer-Encoding: binary\n\n`;

          // Example:
          // "POST https://example.com/_api/web HTTP/1.1"
          batchBody += `${request.method} ${request.url} HTTP/1.1\n`;

          request.headers.forEach((value: string, name: string) => {
            batchBody += `${name}: ${value}\n`;
          });
          batchBody += '\n';
          batchBody += requestText;
          batchBody += '\n\n';
        });
      }
    );

    return chain.then(() => {
      // Close the batch
      batchBody += `--batch_${batchGuid}--\n`;

      return this._digestCache.fetchDigest(this._webUrl);
    }).then((digest: string) => {

      // Start the $batch request
      const headers: Headers = new Headers();
      headers.append('Accept', 'application/json');
      headers.append('OData-Version', '4.0');
      headers.append('Content-Type', `multipart/mixed; boundary=batch_${batchGuid}`);
      headers.append('Content-Length', batchBody.length.toString());
      headers.append('X-RequestDigest', digest);

      const request: Request = new Request(this._webUrl + '/_api/$batch', {
        method: 'POST',
        headers: headers,
        body: batchBody,
        credentials: 'same-origin',
        cache: 'no-cache'
      });

      const promise: Promise<Response> = this._fetchProvider.fetch(request);
      return HttpClientCommon.handleAuthenticationErrors(promise, this._webUrl, this._digestCache);
    }).then((response: Response) => {
      if (!response.ok) {
        throw new Error('ODataBatch: The batch request failed: ' + response.statusText);
      }
      return response.text();
    }).then((text: string) => {
      const responses: Response[] = this._parseResponsesFromBody(text);
      if (responses.length !== this._batchedRequests.length) {
        throw new Error(`ODataBatch: Expecting ${this._batchedRequests.length} responses`);
      }

      for (let i: number = 0; i < this._batchedRequests.length; ++i) {
        const request: ODataBatchedRequest = this._batchedRequests[i];
        request.notifyCompleted(responses[i]);
      }

      return this;
    });
  }

  private _parseResponsesFromBody(text: string): Response[] {
    const responses: Response[] = [];

    const header: string = '--batchresponse_';

    // Ex. "HTTP/1.1 500 Internal Server Error"
    const statusRegExp: RegExp = new RegExp('^HTTP/[0-9.]+ +([0-9]+) +(.*)', 'i');

    const lines: string[] = text.split('\n');

    let parserState: string = 'batch';
    let batchStatus: number;
    let batchStatusText: string;

    for (let i: number = 0; i < lines.length; ++i) {
      const line: string = lines[i];
      switch (parserState) {
        case 'batch':
          if (line.substr(0, header.length) === header) {
            parserState = 'batchHeaders';
          } else {
            if (line.trim() !== '') {
              throw new Error('ODataBatch: Invalid response, line ' + i);
            }
          }
          break;
        case 'batchHeaders':
          if (line.trim() === '') {
            parserState = 'status';
          }
          break;
        case 'status':
          // Ex. "HTTP/1.1 500 Internal Server Error"
          const parts: string[] = statusRegExp.exec(line);
          if (parts.length !== 3) {
            throw new Error('ODataBatch: Invalid status, line ' + i);
          }
          // Ex. "500"
          batchStatus = parseInt(parts[1], 10);
          // Ex. "Internal Server Error"
          batchStatusText = parts[2];
          parserState = 'statusHeaders';
          break;
        case 'statusHeaders':
          if (line.trim() === '') {
            parserState = 'body';
          }
          break;
        case 'body':
          let response: Response;
          if (batchStatus === 204) {
            // https://github.com/whatwg/fetch/issues/178
            response = new Response();
          } else {
            response = new Response(line, { status: batchStatus, statusText: batchStatusText });
          }
          responses.push(response);
          parserState = 'batch';
          break;
      }
    }
    if (parserState !== 'status') {
      throw new Error('ODataBatch: Unexpected end of input');
    }

    return responses;
  }
}

/**
 * This interface is passed to the ODataBatch constructor.  It specifies options
 * that affect the entire batch.
 * @public
 */
export interface IODataBatchOptions {
  /**
   * ODataBatch will need to perform its POST to an endpoint such as
   * "http://example.com/sites/sample/_api/$batch". Typically the SPWeb URL
   * ("https://example.com/sites/sample" in this example) can be guessed by
   * looking for a reserved URL segment such as "_api" in the first URL
   * passed to fetch(), but if not, the webUrl can be explicitly specified
   * using this option.
   */
  webUrl?: string;
}

/**
 * This interface defines the options for an individual REST request that
 * is part of an ODataBatch.  It is based on the WHATWG API standard
 * parameters that are documented here:
 * https://fetch.spec.whatwg.org/
 * @public
 */
export interface IODataBatchRequestOptions extends RequestInit {
  // Reserved for future use
}



