/**
 * Error returned by the rejected promise
 *
 * @alpha
 */
export default class OAuthUtilityError extends Error {
  private _type: OAuthUtilityErrorType;
  private _httpResponseStatus: number;
  private _message: string;
  private _serverErrorCode: number;

  public constructor(type: OAuthUtilityErrorType, context: IOAuthUtilityErrorContext) {
    super(context.message);
    this._type = type;
    this._httpResponseStatus = context.httpResponseStatus;
    this._message = context.message;
    this._serverErrorCode = context.serverErrorCode;
  }

  /**
   * @returns the type of the error. Can be unknownError | malformedResponseBody | serverError
   *
   * @alpha
   */
  public get type(): number {
    return this._type;
  }

  /**
   * serverErrorCode is set only if the type of the {@link OAuthUtilityError} containing this object
   * is serverError.
   * Server error codes are defined in the SPDelegationTokenServiceProviderErrorCodes class
   *
   * @returns server error code. If the error is not of serverError type this method throws
   *
   * @alpha
   */
  public get serverErrorCode(): number {
    if (this.type !== OAuthUtilityErrorType.serverError) {
      throw new Error('ServerErrorCode can only be obtained for errors of type serverError');
    }
    return this._serverErrorCode;
  }

  /**
   * @returns status of the http response (200, 404, etc)
   *
   * @alpha
   */
  public get httpResponseStatus(): number {
    return this._httpResponseStatus;
  }

  /**
   * @returns the error message
   *
   * @alpha
   */
  public get message(): string {
    return this._message;
  }
}

/**
 * Additional error data to be used by the caller
 */
export interface IOAuthUtilityErrorContext {
  httpResponseStatus: number;
  message: string;
  /**
   * serverErrorCode is set only if the type of the {@link OAuthUtilityError} containing this object
   * is OAuthUtilityError.serverError.
   * Server error codes are defined in the SPDelegationTokenServiceProviderErrorCodes class
   */
  serverErrorCode?: number;
}

/**
 * {@link OAuthUtilityError} can have one of the types mentioned in this enum.
 *
 * @alpha
 */
export enum OAuthUtilityErrorType {
  /**
   * unknownError usually means that the http status in the response was neither 400 (server error)
   * or 429 (throttling).
   *
   * @alpha
   */
  unknownError,
  /**
   * malformedResponseBody means that the response that was received (either the success or the error)
   * could not be parsed. This may happen if the server response had it's format changed and the format
   * expected by the library needs to be updated.
   *
   * @alpha
   */
  malformedResponseBody,
  /**
   * serverError means that the server responded with an error. The serverErrorCode is set in this case,
   * and it can be used to identify the reason of the error.
   *
   * {@internalremark The serverside error codes are defined in the class SPDelegationTokenServiceProviderErrorCodes}
   *
   * @alpha
   */
  serverError
}




