import ServiceKey from '../serviceScope/ServiceKey';
import ServiceScope from '../serviceScope/ServiceScope';
import * as lodash from '@microsoft/sp-lodash-subset';
import { IFetchProvider, fetchProviderServiceKey  } from './FetchProvider';

/**
 * BasicHttpClient implements a basic set of features for performing REST operations.
 * The subclass HttpClient extends this basic functionality with SharePoint-specific
 * enhancements.
 * @public
 */
export default class BasicHttpClient {
  private _fetchProvider: IFetchProvider;
  private _serviceScope: ServiceScope;

  constructor(serviceScope: ServiceScope) {
    this._serviceScope = serviceScope;
    serviceScope.whenFinished(() => {
      this._fetchProvider = serviceScope.consume(fetchProviderServiceKey);
    });
  }

  protected get serviceScope(): ServiceScope {
    return this._serviceScope;
  }

  /**
   * Performs a REST service call.  Although the HttpClient subclass adds
   * additional enhancements, the parameters and semantics for BasicHttpClient.fetch()
   * are essentially the same as the WHATWG API standard that is documented here:
   * https://fetch.spec.whatwg.org/
   * @params url - the URL to fetch
   * @params options - additional options that affect the request
   * @returns a promise that will return the result
   */
  public fetch(url: string, options: IBasicHttpClientOptions): Promise<Response> {
    return this.fetchCore(new Request(url, options));
  }

  /**
   * Calls fetch(), but sets the method to 'GET'.
   * @params url - the URL to fetch
   * @params options - additional options that affect the request
   * @returns a promise that will return the result
   */
  public get(url: string, options?: IBasicHttpClientOptions): Promise<Response> {
    const modifiedOptions: IBasicHttpClientOptions = lodash.clone(options || {});
    modifiedOptions.method = 'GET';
    return this.fetch(url, modifiedOptions);
  }

  /**
   * Calls fetch(), but sets the method to 'POST'.
   * @params url - the URL to fetch
   * @params options - additional options that affect the request
   * @returns a promise that will return the result
   */
  public post(url: string, options: IBasicHttpClientOptions): Promise<Response> {
    const modifiedOptions: IBasicHttpClientOptions = lodash.clone(options || {});
    modifiedOptions.method = 'POST';
    return this.fetch(url, modifiedOptions);
  }

  protected fetchCore(request: Request): Promise<Response> {
    // QOS logging will be added here (VSO Task #163782)
    console.log(`HttpClient: ${request.method} ${request.url}`);

    return this._fetchProvider.fetch(request);
  }
}

export const basicHttpClientServiceKey: ServiceKey<BasicHttpClient> = ServiceKey.create<BasicHttpClient>
  ('sp-client-base:BasicHttpClient', BasicHttpClient);

/**
 * This interface defines the options for the BasicHttpClient operations such as
 * get(), post(), fetch(), etc.  It is based on the whatwg API standard
 * parameters that are documented here:
 * https://fetch.spec.whatwg.org/
 * @public
 */
export interface IBasicHttpClientOptions extends RequestInit {
  // Reserved for future usage
}



